.c-btn{@apply .px-4 .py-2 .rounded .bg-grey-lighter;}.c-btn:hover{@apply .cursor-pointer;}.c-btn.block{@apply .w-full;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.primary{@apply .bg-blue .text-white;}.c-btn.warning{@apply .bg-orange .text-white;}.c-btn.danger{@apply .bg-red .text-white;}.c-btn[disabled]{@apply .opacity-50 .cursor-default;}.c-btn.outline{@apply .bg-transparent;}.c-btn.outline.primary{@apply .border .border-solid .border-blue .text-blue;}.c-btn.outline.warning{@apply .border .border-solid .border-orange .text-orange;}.c-btn.outline.danger{@apply .border .border-solid .border-red .text-red;}